<template>
	<div id="shop-location">
		<public-header title="选择地址"></public-header>
		<div class="content">
			<div class='search-header-wrap van-hairline--bottom'>
				<div class='search-header'>
					<van-search v-model="key" show-action background="#fff" placeholder="请输入您的地址">
						<div slot="action">搜索</div>
					</van-search>
				</div>
			</div>
			<template v-if="!showSearch">
				<div class='location' @click="onGetNowLocation">
					<van-icon name='location'></van-icon> {{getNowLocationing == true ? '正在获取定位...' : '点击定位当前位置'}}
				</div>
				<template v-if="addresses.length && addresses.length > 0">
					<div class="item-title">
						<van-icon name='time'></van-icon>附近的地址
					</div>
					<van-cell-group>
						<van-cell v-for="(item, index) in addresses" :title="item.address" :label="item.name" :key="index" @click="chooseLocation(item)"/>
					</van-cell-group>
				</template>

				<template v-if="addressSearchHistory.length && addressSearchHistory.length > 0">
					<div class="item-title">
						<van-icon name='location'></van-icon>搜索历史
					</div>
					<van-cell-group>
						<van-cell :title="item.address" :label="item.name" v-for="(item, index) in addressSearchHistory" :key="index" @click="chooseLocation(item)"/>
					</van-cell-group>
				</template>
			</template>
			<van-cell-group v-else>
				<van-cell v-for="(item,index) in searchAddress"  :title="item.address"  :label="item.name" :key="index" @click="chooseLocation(item, true)"/>
			</van-cell-group>
		</div>
	</div>
</template>

<script>
	import { mapState, mapMutations } from "vuex"
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				addresses: [],
				showSearch: false,
				key: '',
				searchAddress: [],
				addressSearchHistory: [],
				getNowLocationing: false,
				MapType: 'gaode',
			}
		},
		components: {
			PublicHeader,
		},
		watch: {
			key() {
				let key= this.key;
				this.showSearch = key ? true : false;
				this.searchAddress = [];
				var url = 'system/common/map/suggestion';
				if(this.MapType == 'google') {
					url = 'system/common/map/suggestion_google';
				}
				if(key) {
					this.util.request({
						url: url,
						data: {
							key: key
						}
					}).then((res) => {
						let result = res.data.message;
						if(result.errno) {
							return;
						}
						this.searchAddress = [...this.searchAddress, ...result.message];
					})
				}
			}
		},
		methods: {
			...mapMutations(['replaceState', 'getLocation', 'setLocation']),

			onLoad() {
				var that = this;
				that.MapType = that.util.getStorage('MapType');
				var addressSearchHistory = that.util.getStorage('addressSearchHistory')
				if(addressSearchHistory && addressSearchHistory.length && addressSearchHistory.length > 0) {
					that.addressSearchHistory = [...that.addressSearchHistory, ...addressSearchHistory];
				}

				that.getLocation();
				if(!that.locationInfo.location_x) {
					that.util.getLocation({
						successLocation: function(res) {
							that.setLocation({
								location_x: res.location_x,
								location_y: res.location_y
							});
							that.onGetPos();
						},
						fail: function(res) {
							that.util.$toast('获取定位失败, 你可以手动收缩地址');
						}
					});
				} else {
					that.onGetPos();
				}
			},

			onGetPos() {
				var that = this;
				let url = 'system/common/map/regeo';
				if(that.MapType == 'google') {
					url = 'system/common/map/regeo_google';
				}
				that.util.request({
					url: url,
					data: {
						latitude: that.locationInfo.location_x,
						longitude: that.locationInfo.location_y,
						convert: 0
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						return;
					}
					result = result.message;
					if(result.pois && result.pois.length > 0) {
						that.addresses = [...result.pois];
					}
				})
			},

			chooseLocation(item, is_search = false) {
				var locationInfo = {
					address: item.address,
					location_x: item.location_x,
					location_y: item.location_y,
				}
				this.replaceState({key: 'searchLocationInfo', val: locationInfo});
				if(is_search) {
					locationInfo['name'] = item.name;
					this.addressSearchHistory.push(locationInfo);
					this.util.setStorage('addressSearchHistory', this.addressSearchHistory);
				}
				this.$router.go(-1);
			},

			onGetNowLocation() {
				var that = this;
				that.getNowLocationing = true;
				that.util.getLocation({
					successLocation: function(res) {
					},
					successAddress:  function(res) {
						that.replaceState({key: 'searchLocationInfo', val: locationInfo});
						that.getNowLocationing = false;
						that.$router.go(-1);
					},
					fail: function(res) {
						that.getNowLocationing = false;
						that.util.$toast('获取定位失败,你可以手动搜索地址');
					},
					MapType: that.MapType
				});
			},
		},
		computed: {
			...mapState([
				'searchLocationInfo', 'locationInfo'
			]),
		},
		mounted() {
			this.onLoad();
		},
	}
</script>

<style>
	#shop-location .content{
		position: fixed;
		top: 46px;
		right: 0;
		left: 0;
		bottom: 0;
		width: 100%;
		background: #f5f5f5;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#shop-location .content::-webkit-scrollbar {
		display: none;
	}
	#shop-location .content .van-search{
		padding: 6px 10px;
	}
	#shop-location .content .van-search__action{
		padding-right: 0!important;
	}
	#shop-location .content .search-header-wrap .search-header .van-cell{
		background-color:#f5f5f5;
		border-radius: 20px;
	}
	#shop-location .content .search-header-wrap .search-header .van-cell input{
		background-color:#f5f5f5;
	}
	#shop-location .content .search-header-wrap .search-header .van-search__action{
		font-size:14px;
		color:#FF2D4B;
		display:inline-block;
		padding: 0 9px;
	}
	#shop-location .content .location{
		text-align:center;
		padding:10px;
		background:#fff;
		margin-top:10px;
		font-size:15px;
		line-height: 24px;
	}
	#shop-location .content .location .van-icon{
		font-size: 14px;
	}
	#shop-location .content .item-title{
		padding:10px 0 10px 15px;
		font-size:14px;
		color:#B2B2B2;
		line-height: 22px;
	}
	#shop-location .content .item{
		background: #fff;
	}
	#shop-location .content .van-cell-group .van-cell__title span{
		line-height: 22px;
		font-size: 14px;
	}
	#shop-location .content .van-cell-group .van-cell__title div{
		margin-top:5px;
		color:#B2B2B2;
		line-height: 22px;
		font-size: 14px;
	}
	#shop-location .content .van-search .van-icon-clear{
		display: none;
	}
</style>


